import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { HomePageComponent } from './home-page/home-page.component';
import { LoginComponent } from './login/login.component';
import { ProductDetailsComponent } from './products/product-details/product-details.component';
import { ProductsComponent } from './products/products.component';
import { ShoppingcartComponent } from './shoppingCart/shoppingcart/shoppingcart.component';
import { CheckoutComponent } from './_CheckoutOrders/checkout.component';
import { AuthGuard } from './_guards/auth.guard';

const routes: Routes = [
  {path:'',component:HomePageComponent,pathMatch:"full"},
  {path:'products',component:ProductsComponent,canActivate:[AuthGuard]},
  {path:'products/:id',component:ProductDetailsComponent,canActivate:[AuthGuard]},
  {path:'login',component:LoginComponent},
  {path:'cart',component:ShoppingcartComponent,canActivate:[AuthGuard]},
  {path:'checkout',component:CheckoutComponent,canActivate:[AuthGuard]},
  
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
export const routingComponents = [ProductsComponent,LoginComponent,HomePageComponent,ShoppingcartComponent,CheckoutComponent]
