import { Component, OnInit, ViewChild } from '@angular/core';
import { IBrands } from '../_Models/Brands';
import { ICategory } from '../_Models/Categories';
import { IPagination } from '../_Models/Paganation';
import { IProducts } from '../_Models/Products';
import { ProductsService } from '../_services/products.service';

@Component({
  selector: 'app-products',
  templateUrl: './products.component.html',
  styleUrls: ['./products.component.scss']
})
export class ProductsComponent implements OnInit {
  Products:IProducts[];
  Category:ICategory[]=[];
  Brands:IBrands[]=[];
  p:IPagination;
  pageNumber = 1
  PageSize = 6
  TotalItems = 50
  constructor(private ProductService:ProductsService) { }

  ngOnInit(): void {
    this.getAllProducts();
    this.getAllCategory();
    this.getAllBrands();
  }

  getAllProducts(){
    this.ProductService.getAllProducts(this.pageNumber,this.PageSize,this.TotalItems).subscribe(results =>{
      this.Products = results.result
      this.p = results.Pagination
    },error =>{
      console.log(error);
    })
  }

  pageChanged(event: any){
    this.pageNumber = event.page;
    this.getAllProducts();
  }

  getAllCategory(){
    this.ProductService.getAllCategory().subscribe(results =>{
      this.Category = results
    },error =>{
      console.log(error);
    })
  }

  getAllBrands(){
    this.ProductService.getAllBrands().subscribe(results => {
      this.Brands = results
    },error =>{
      console.log(error);
    })
  }

}
