import { AfterViewInit, Component, ElementRef, Input, OnDestroy, OnInit, Output, ViewChild } from '@angular/core';
import { FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { timingSafeEqual } from 'crypto';
import { element } from 'protractor';
import { ICart } from '../_Models/Cart';
import { IDelivery } from '../_Models/Delivery';
import { IActualOrder } from '../_Models/order';
import { CheckoutService } from '../_services/checkout.service';
import { ShoppingCartService } from '../_services/shopping-cart.service';
declare var Stripe
@Component({
  selector: 'app-checkout',
  templateUrl: './checkout.component.html',
  styleUrls: ['./checkout.component.scss']
})
export class CheckoutComponent implements OnInit, AfterViewInit, OnDestroy {
 checkout:FormGroup
 delivery?:IDelivery[];
 deliId:IDelivery
 @Input() deliveryInput:FormGroup
 @ViewChild('cardNumber',{static:true}) cardNumberEleRef:ElementRef
 @ViewChild('cardExpiry',{static:true}) expiredEleRef:ElementRef
 @ViewChild('cardCvc',{static:true}) cvvEleRef:ElementRef
 stripe:any
 cardNumber:any
 expired:any
 CVC:any
 cardErrors:any
 cardHandler = this.onChange.bind(this);
 cardNumberValid = false
 expiredValid = false
 CVCValid = false
 loading = false
  constructor(private formGroup:FormBuilder,private cartService:ShoppingCartService,private checkService:CheckoutService) { }

  ngOnInit():void{
    this.createForm()
  }

  ngAfterViewInit(): void {
    this.stripe = Stripe('pk_test_51JSlk9HqZ5nbmm9CuvZmEK0lK7zXJXc8uXhQs8Kyj3i1xqMukDhdNDpyCyXLMDUf9DDbGTPfWhT959JSEbQsjxVv00gFlfqhVu');
    const elements = this.stripe.elements();

    this.cardNumber = elements.create('cardNumber')
    this.cardNumber.mount(this.cardNumberEleRef.nativeElement)
    this.cardNumber.addEventListener('change',this.cardHandler)
     
    this.expired = elements.create('cardExpiry')
    this.expired.mount(this.expiredEleRef.nativeElement)
    this.expired.addEventListener('change',this.cardHandler)

    this.CVC = elements.create('cardCvc')
    this.CVC.mount(this.cvvEleRef.nativeElement)
    this.CVC.addEventListener('change',this.cardHandler)
 
  }
  ngOnDestroy(){
    this.cardNumber.Destroy()
    this.expired.Destroy()
    this.CVC.Destroy()
  }

  onChange(event){
    if(event.error){
      this.cardErrors = event.error.message
    }else{
      this.cardErrors = null
    }
    switch (event.elementType) {
      case 'cardNumber':
        this.cardNumberValid = event.complete;
        break;
      case 'cardExpiry':
        this.expiredValid = event.complete;
        break;
      case 'cardCvc':
        this.CVCValid = event.complete;
        break;
      default:
        break;
    }
  }

  setDeliveryValue(deli?:IDelivery){
    this.cartService.getShippingPrice(deli)
  }



    createForm(){
    this.checkout = new FormGroup({
      firstName: new FormControl('',Validators.required),
      lastName: new FormControl('',Validators.required),
      street: new FormControl('',Validators.required),
      city: new FormControl('',Validators.required),
      state: new FormControl('',Validators.required),
      zipCode: new FormControl('',Validators.required),
      nameOnCard: new FormControl('',Validators.required)
    })
  }


  async onSubmit(){
    this.loading = true;
    const Cart = this.cartService.cartValue()
    try {
      const actualOrder = await this.createOrder(Cart)
      const paymentResult = await this.confrimPayment(Cart)
      if(paymentResult.paymentIntent){
        this.cartService.deleteCart(Cart)
       }else{
       console.log(paymentResult.error.message)
      }
      this.loading = false
    } catch (error) {
      console.log(error)
      this.loading =false
    }

  }

  private async confrimPayment(Cart) {
    return this.stripe.confirmCardPayment(Cart.clientSecret,{
      payment_method:{
        card: this.cardNumber,
        billing_details:{
          name:this.checkout.get('nameOnCard').value
        }
      }
    })
  }
  private async createOrder(Cart: ICart) {
    const order = this.getOrder(Cart)
    return this.checkService.createOrder(order).toPromise()
  }

  private getOrder(Cart: ICart) {
    return{
      cartId:Cart.shopCartId,
      specialDeliveryID:this.deliId?.deliveryId,
      shiptoAddress:this.checkout.value
    }
  }
  
}
