import { Component, OnInit } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { Observable } from 'rxjs';
import { ICart, ICartItems } from 'src/app/_Models/Cart';
import { IDelivery } from 'src/app/_Models/Delivery';
import { CheckoutService } from 'src/app/_services/checkout.service';
import { ShoppingCartService } from 'src/app/_services/shopping-cart.service';


@Component({
  selector: 'app-shoppingcart',
  templateUrl: './shoppingcart.component.html',
  styleUrls: ['./shoppingcart.component.scss']
})
export class ShoppingcartComponent implements OnInit {
  Cart$:Observable<ICart>
  Cart:ICart
  deliveryInput:FormGroup
  delivery?:IDelivery[];

  constructor(private CartService:ShoppingCartService,private checkService:CheckoutService) { }

  ngOnInit(): void {
    this.Cart$ = this.CartService.Cart$
    this.getDelivery()
    this.createDelivery()
    this.getdeliveryValue()
  }

  createDelivery(){
    this.deliveryInput = new FormGroup({
      deli:new FormControl('',Validators.required)
    });
  }

  setDeliveryValue(deli?:IDelivery){
    this.CartService.getShippingPrice(deli)
  }

  getdeliveryValue(){
    const Cart = this.CartService.cartValue()
    if(Cart.deliveryId !== null){
      this.deliveryInput.get('deli').patchValue(Cart.deliveryId?.toString());
    }
  }

  getDelivery(){
    this.checkService.getDelivery().subscribe((result:IDelivery[]) => {
      this.delivery = result;
    },error =>{
      console.log(error);
    })
  }
  
  removeItem(item:ICartItems){
    this.CartService.removeItem(item)
  }

  increment(item:ICartItems){
    this.CartService.increment(item)
  }

  decrement(item:ICartItems){
    this.CartService.decrement(item)
  }

  createPayment(){
    return this.CartService.createPaymentIntent().subscribe((results:any) =>{
      this.Cart = results
    },error =>{
      console.log(error);
    })
  }

}
