import { HttpClient } from '@angular/common/http';

import { Injectable } from '@angular/core';
import { Router } from '@angular/router';
import { ReplaySubject } from 'rxjs';
import { map } from 'rxjs/operators';
import { environment } from 'src/environments/environment';
import { IAddress } from '../_Models/Address';
import { IUserAccount } from '../_Models/Login';

@Injectable({
  providedIn: 'root'
})
export class LoginService {
  baseUrl = environment.baseUrl;
  public CurrentUser = new ReplaySubject<IUserAccount>(1);
  CurrentUser$ = this.CurrentUser.asObservable();
  constructor(private http:HttpClient,private router:Router) { }
  user:IUserAccount

  login(values:any){
    return this.http.post<IUserAccount>(this.baseUrl + 'Account/Login',values).pipe(
      map((reponse:IUserAccount)=>{
        const user = reponse
        if(user){
          localStorage.setItem('user',JSON.stringify(user))
          this.setCurrentUser(user);
        }
      })
    )
  }
  
  setCurrentUser(user:IUserAccount){
    this.CurrentUser.next(user);
  }

  logOut(){
    localStorage.removeItem('user');
    this.router.navigateByUrl('/login')
    this.CurrentUser.next(null);
  }

  getUserAddress(id:number){
    return this.http.get(this.baseUrl + 'Account/Address' + id);
  }

  updateAddress(address:IAddress){
    return this.http.put<IAddress>(this.baseUrl + 'Account/Address',address);
  }

}
