import {v4 as uuidv4} from 'uuid';
export interface ICartItems{
    cartItemsId: number;
    itemName: string;
    imageUrl: string;
    description: string;
    price: number;
    amount: number;
    category: string;
    brand: string;

}

export interface ICart {
    shopCartId: string;
    cartItems: ICartItems[];
    deliveryId?:number;
    clientSecret?:string;
    paymentID?:string;
    deliPrice?:number
}

export class ShoppingCart implements ICart{
    deliveryId?: number;
    clientSecret?: string;
    paymentID?: string;
    deliPrice?:number
    shopCartId = uuidv4();
    cartItems: ICartItems[] = [];
}

export interface ICartTotal{
    shipping:number
    subTotal:number
    total:number
}