import { Component, Input, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';

import { IProducts } from 'src/app/_Models/Products';
import { ProductsService } from 'src/app/_services/products.service';
import { ShoppingCartService } from 'src/app/_services/shopping-cart.service';

@Component({
  selector: 'app-product-details',
  templateUrl: './product-details.component.html',
  styleUrls: ['./product-details.component.scss']
})
export class ProductDetailsComponent implements OnInit {
  @Input() products?: IProducts;
  amount = 0
  constructor(private activeRoute:ActivatedRoute,private productService:ProductsService,private CartService:ShoppingCartService) { }

  ngOnInit(): void {
    this.LoadProduct();
  
  }

  LoadProduct(){
    this.productService.getProduct(+this.activeRoute.snapshot.paramMap.get('id')).subscribe(result => {
      this.products = result
    },error =>{
      console.log(error)
    })
  }

  addItems(){
    this.CartService.addToCart(this.products,this.amount)
  }

  incrementAmount(){
    this.amount++
  }
  decrementAmount(){
    this.amount--
  }

}
