import { ThisReceiver } from '@angular/compiler';
import { Component, OnInit } from '@angular/core';
import { IUserAccount } from './_Models/Login';
import { LoginService } from './_services/login.service';
import { ShoppingCartService } from './_services/shopping-cart.service';


@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss']
})
export class AppComponent implements OnInit {
  title = 'DreamBig';
  user:any;

  constructor(private accountService:LoginService,private CartServive:ShoppingCartService) {
  }
  ngOnInit() {
    this.setCurrentUser();
    this.setCart();
  
  }
  
  setCurrentUser(){
    const user: IUserAccount = JSON.parse(localStorage.getItem('user'));
    this.accountService.setCurrentUser(user);
  }

  setCart(){
    const CartId = localStorage.getItem('ShoppingCart_id')
    if(CartId){
      this.CartServive.getShoppingCart(CartId).subscribe(()=>{
        console.log('Initialize Shopping Cart')
      },error =>{
        console.log(error)
      })
    }
  }

 
}
