import { HttpClient, HttpParams} from '@angular/common/http';
import { Injectable } from '@angular/core';
import { environment } from 'src/environments/environment';
import { ICategory } from '../_Models/Categories';
import { PaginatedResults, Pagination } from '../_Models/Paganation';
import { IProducts } from '../_Models/Products';
import { map } from 'rxjs/operators';
import { IBrands } from '../_Models/Brands';
import { of } from 'rxjs';
import { ShopParams } from '../_Models/ShopParams';

@Injectable({
  providedIn: 'root'
})
export class ProductsService {
  baseUrl = environment.baseUrl;
  PaginatedResult:PaginatedResults<IProducts[]> = new PaginatedResults<IProducts[]>()
  products:IProducts[] = []
  category:ICategory[] = []
  brand:IBrands[] = []
  // shopParams = new ShopParams()
  // pagination = new Pagination()
  constructor(private http:HttpClient) { }

  getAllProducts(page?:number,ItemsPerPage?:number,TotalItems?:number){
    let params = new HttpParams();
    
    if(page !== undefined && ItemsPerPage !==undefined)
    {
      params = params.append('PageNumber',page.toString())
      params = params.append('PageSize',ItemsPerPage.toString())
      params = params.append('TotalItems',TotalItems.toString())
    }
    return this.http.get<IProducts[]>(this.baseUrl + 'Products',{observe:'response',params})
    .pipe(
      map(response =>{
      this.PaginatedResult.result = response.body;
      if(response.headers.get('Pagination') !== undefined){
        this.PaginatedResult.Pagination = JSON.parse(response.headers.get('Pagination'));
      }
      return this.PaginatedResult
    }))
    
  }

  getProduct(id:number){
    const product = this.products.find(p => p.productId === id)
    if(product) return of(product) 
    return this.http.get<IProducts>(this.baseUrl + 'Products/' + id )
  }

  getAllBrands(){
    if(this.brand.length > 0){
      return of(this.brand)
    }
    return this.http.get<IBrands[]>(this.baseUrl + 'Products/Brand').pipe(
      map(response =>{
        this.brand = response
        return response
      })
    );
  }

  getAllCategory(){
    if(this.category.length > 0){
      return of(this.category)
    }
    return this.http.get<ICategory[]>(this.baseUrl + 'Products/Category').pipe(
      map(response =>{
        this.category = response
        return response
      })
    );
  }


}
