import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { BehaviorSubject } from 'rxjs';
import { map } from 'rxjs/operators';
import { environment } from 'src/environments/environment';
import { ICart, ICartItems, ICartTotal, ShoppingCart } from '../_Models/Cart';
import { IDelivery } from '../_Models/Delivery';
import { IProducts } from '../_Models/Products';

@Injectable({
  providedIn: 'root'
})
export class ShoppingCartService {
  baseUrl = environment.baseUrl;
  private CartSource = new BehaviorSubject<ICart>(null);
  Cart$ = this.CartSource.asObservable();
  private CartTotalSource = new BehaviorSubject<ICartTotal>(null);
  CartTotal$ = this.CartTotalSource.asObservable();
  shipping = 0
  constructor(private http:HttpClient) { }

  createPaymentIntent(){
    return this.http.post(this.baseUrl + 'Payment/' + this.cartValue().shopCartId,{})
    .pipe(map((cart:ICart) => {
      this.CartSource.next(cart)
      console.log(cart)
    }))
  }

  getShoppingCart(Id:string){
    return this.http.get(this.baseUrl + 'ShoppingCart?id=' + Id).pipe(
      map((Cart?:ICart) =>{
          this.CartSource.next(Cart)
          this.calcualteTotals()
          console.log(this.cartValue())
      })
    );
  }

  getShippingPrice(deliPrice:IDelivery){
    this.shipping = deliPrice.price
    const Cart = this.cartValue();
    Cart.deliveryId = deliPrice.deliveryId;
    Cart.deliPrice = deliPrice.price
    this.calcualteTotals()
    this.setShoppingCart(Cart);
  }
  

  setShoppingCart(Cart:ICart){
    return this.http.post<ICart>(this.baseUrl + 'ShoppingCart', Cart).subscribe((result:ICart) =>{
      this.CartSource.next(result);
      this.calcualteTotals();
      console.log(result);
    },error =>{
      console.log(error);
    })
  }

  cartValue(){
    return this.CartSource.value;
  }

  addToCart(item:IProducts, amount = 1){
    const itemToAdd:ICartItems = this.mappedCart(item,amount)
    const ShoppingCart = this.cartValue() ?? this.createACart()
    ShoppingCart.cartItems = this.addOrUpdateItems(ShoppingCart.cartItems,itemToAdd,amount)
    this.setShoppingCart(ShoppingCart)
  }

  increment(item:ICartItems){
    const Cart = this.cartValue()
    const objtofind = Cart.cartItems.findIndex(x => x.cartItemsId === item.cartItemsId)
    Cart.cartItems[objtofind].amount++;
    this.setShoppingCart(Cart);
  }

  decrement(item:ICartItems){
    const Cart = this.cartValue()
    const objtofind = Cart.cartItems.findIndex(x => x.cartItemsId === item.cartItemsId)
    if(Cart.cartItems[objtofind].amount > 1){
      Cart.cartItems[objtofind].amount--;
      this.setShoppingCart(Cart);
    }else{
      this.removeItem(item)
    }
   
  }

  removeItem(item: ICartItems) {
    const Cart = this.cartValue()
    if(Cart.cartItems.some(x => x.cartItemsId === item.cartItemsId)){
      Cart.cartItems = Cart.cartItems.filter(x => x.cartItemsId !== item.cartItemsId)
      if(Cart.cartItems.length > 0){
        this.setShoppingCart(Cart)
      }else{
        this.deleteCart(Cart)
      }  
    }
  }

  deletelocalCart(id:string){
    this.CartSource.next(null)
    this.CartTotalSource.next(null)
    localStorage.removeItem('ShoppingCart_id')
  }

  deleteCart(Cart: ICart) {
    return this.http.delete(this.baseUrl + 'ShoppingCart?Id='+ Cart.shopCartId).subscribe(()=>{
      this.CartTotalSource.next(null)
      this.CartSource.next(null)
      localStorage.removeItem('ShoppingCart_id')
    },error => {
      console.log(error)
    })
  }

  //calulate shopping cart total
  private calcualteTotals(){
    const Cart = this.cartValue();
    const shipping = this.shipping;
    const subTotal = Cart.cartItems.reduce((a,b)=>(b.price * b.amount) + a,0);
    const total = shipping + subTotal;
    this.CartTotalSource.next({shipping,subTotal,total});
  }

  private addOrUpdateItems(Items: ICartItems[], itemToAdd: ICartItems, amount: number): ICartItems[] {
    const index = Items.findIndex(x => x.cartItemsId === itemToAdd.cartItemsId)
    if(index === -1){
      itemToAdd.amount = amount
      Items.push(itemToAdd)
    }else{
      Items[index].amount += amount
    }
    return Items
  }
  
  private createACart(): ICart {
    const Cart = new ShoppingCart()
    localStorage.setItem('ShoppingCart_id',Cart.shopCartId)
    return Cart;
  }
  
  private mappedCart(item: IProducts, amount: number): ICartItems {
    return{
      cartItemsId:item?.productId,
      itemName:item?.productName,
      imageUrl:item?.imageUrl,
      description:item?.Description,
      price:item?.price,
      amount,
      category:item?.category,
      brand:item?.brand
    }
  }
}
